}

.dataTable-wrapper .dataTable-top .dataTable-search input:focus-visible {
  outline: none;
}

.dataTable-wrapper .dataTable-container .table thead tr th {
  padding: 0.75rem 1.5rem;
  opacity: .7;
  font-weight: bolder;
